@keyframes lefttoright {
    0% {
        opacity: 0;
        transform: translateX(-10rem); }
    100% {
        opacity: 1;
        transform: translateX(00rem); } }
@keyframes righttoleft {
    0% {
        opacity: 0;
        transform: translateX(10rem); }
    100% {
        opacity: 1;
        transform: translateX(00rem); } }
@keyframes btntoup {
    0% {
        opacity: 0;
        transform: translateY(10rem); }
    100% {
        opacity: 1;
        transform: translateY(00rem); } }
